import Navbar from "./Navbar";
import {useState} from "react";
import MenuIcon from '@mui/icons-material/Menu';
import CloseIcon from '@mui/icons-material/Close';
function Navbar_mobile() {
    const [navbar, setNavbar] = useState(true);
    const [navbarMobile, setNavbarMobile] = useState(true);
    return (
        <div className={"navbar-bottom"}>
            {navbar ? (
                            <div onClick={()=>{setNavbar(false);}}> 
                                <MenuIcon sx={{ color: "#08fdd8" }}/>
                            </div>
                        ) : (
                            <div className={"navbar_mobile_container"}>
                                <div onClick={()=>{setNavbar(true);}}>
                                    <CloseIcon sx={{ color: "#08fdd8" }}/></div>
                                    <Navbar/>
                            </div>
                        ) 
            }
        </div>
    );
}

export default Navbar_mobile;